.singleProduct {
  .container {
    display: flex;
    padding: 10px 4px;
    margin-top: 1rem;
    gap: 4rem;

    .left {
      flex: 2;
      height: 100%;
      overflow: hidden;

      .sliderImage {
        display: flex;
        align-items: center;
        gap: 15px;

        .images {
          //   width: 9vw;
          // width: 24%;
          // height: 18vh;
          display: flex;
          flex-direction: row;
          gap: 15px;
          flex-wrap: wrap;

          img {
            width: 7rem;
            // height: 6rem;
            object-fit: cover;
            cursor: pointer;
          }
        }

        .mainImage {
          //   width: 34vw;


          img {
            width: 30rem;
            height: 43rem;
            object-fit: cover;
          }
        }
      }
    }

    .right {
      flex: 2;
      height: 100%;

      .details {
        h3 {
          font-size: 28px;
          font-weight: 600;
        }

        div {
          display: flex;
          justify-content: flex-start;
          gap: 7rem;
          align-items: center;

          del {
            font-size: 19px;
            color: #a59494;
            font-weight: 400;
          }

          span {
            color: #7575ff;
            font-size: 23px;
            font-weight: 500;
          }
        }

        p {
          font-size: 18px;
          color: gray;
          font-weight: 400;
          text-align: justify;
          margin-top: 2rem;
          line-height: 30px;
        }
      }

      .addToCart {
        .add {
          display: flex;
          align-items: center;
          gap: 20px;

          button {
            background-color: #d9dddf;
            color: black;
            font-size: 20px;
            font-weight: 300;
            padding: 4px 20px;
            cursor: pointer;
            border: none;
          }
        }

        .addToCart {
          background-color: #2879fe;
          color: #fff;
          font-size: 16px;
          font-weight: 500;
          padding: 13px 36px;
          cursor: pointer;
          border: none;
          margin-top: 1rem;
          /* margin-bottom: 4rem; */
          border-radius: 3px;
        }

        .buyLater {
          margin-top: 1rem;
          padding-top: 1rem;

          a {
            color: #2879fe;
            font-size: 13px;
            margin-right: 2rem;
            font-weight: 600;
            border: none;
          }
        }
      }

      .description {
        p {
          color: #b1a8a8;
          font-size: 13px;
          font-weight: 500;
        }

        .top {
          border-bottom: 1px solid #b1a8a8;
          width: 95%;
          line-height: 24px;
          padding-bottom: 2rem;
        }

        .bottom {
          margin-top: 2rem;
          display: flex;
          flex-direction: column;
          gap: 20px;
          color: #b1a8a8;
          font-size: 13px;
          font-weight: 500;

          span {
            border-bottom: 1px solid #b1a8a8;
            width: 26%;
            padding-bottom: 1rem;
          }
        }
      }
    }
  }
}