.categories {
  .container {
    display: flex;
    margin: 100px 0;
    height: 80vh;
    gap: 9px;
    overflow: hidden;
    margin-top: 10rem;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 4px;
    }
    .col {
      flex: 1;
      display: flex;
      flex-direction: column;
      gap: 9px;
    }
    .col-l {
      flex: 2;
    }
    .row {
      flex: 1;
      display: flex;
      gap: 9px;
      height: 40vh;
      position: relative;

      a {
        position: absolute;
        left: 50%;
        top: 50%;
        margin: auto;
        text-align: center;
        display: flex;
        /* width: 28px; */
        padding: 12px 28px;
        text-align: center;
        outline: none;
        border: none;
        font-size: 15px;
        border-radius: 4px;
        cursor: pointer;
        background-color: #fff;
      }
    }
  }
}
